.container {
overflow: hidden;
width: 265px;
margin: auto;
margin-top: 50px;
}
input {
  display: none;
}
label {
  float: right;
  width: 42px;
  font-size: 50px;
  cursor: pointer;
  line-height: 50px;
  color: gray;
}
label::before {
  content:'\2605';
}
input:checked ~ label {
  color: gold;
}
label:active {
  position: relative;
}



